import { Component, OnInit, ViewChild } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { UtilsService } from 'src/app/services/utils.service';
import { AppValueService } from 'src/app/services/app-value.service';
import { TranslateService } from '@ngx-translate/core';
import { ModalSummaryDetailComponent } from 'src/app/components/modal-summary-detail/modal-summary-detail.component';
import { SummaryManagementService } from './summary-management.service';

@Component({
  selector: 'app-summary-management',
  templateUrl: './summary-management.page.html',
  styleUrls: ['./summary-management.page.scss'],
})
export class SummaryManagementPage implements OnInit {
  @ViewChild('contentSearch') contentSearch: any;  
  heightTable = "775";
  constructor(private popoverController: PopoverController,
    public summaryService: SummaryManagementService,
    public utilsService: UtilsService,
    public appValueService: AppValueService,
    private translate: TranslateService) { }

  ngOnInit() {
  }
  showDetail(data: any) {
    this.showModalEditProduct(data.ev, data.row._row.data);
  }

  async showModalEditProduct(ev: any, row: any) {
    const popover = await this.popoverController.create({
      component: ModalSummaryDetailComponent,
      event: ev,
      translucent: true,
      componentProps: { rowProduct: row },
      cssClass: "edit-category-popover"
    });
    return await popover.present();
  }
  ionViewDidEnter() {
    this.heightTable = this.contentSearch.el.offsetHeight;
  }
}
